.row{margin-left: 20px;
}

.Movies{
    display:flex;
    padding: 20px;
    overflow-x: scroll;
    overflow-y: hidden;  
}
.Movies::-webkit-scrollbar{
    display: none;
}
.Movie{
    height:200px;
    max-height:250px;
    margin-right: 10px;
    transition: transform 450ms;
}
.Movie:hover{
 transform: scale(1.1);
}
.largeMovie{
   max-height:380px;
   min-height: 330px;
}